.itemList {
    background: rgba(255, 255, 255, .5);
    border: 1px solid grey;
    border-radius: 0 0 12px 12px;
    list-style: none;
    padding: 0;
    min-height: 400px;
}

.itemList li {
    width: 100%;
    border-bottom: 1px solid grey;
    padding: 10px 0px 10px 30px;
    font-size: 18px;
    cursor: pointer;
}

input {
    width: 100%;
    border-radius: 12px 12px 0 0;
    border-bottom: 1px solid grey;
    padding: 10px 0px 10px 30px;
    font-size: 18px;
    cursor: pointer;
}

.itemList li:hover {
    background: rgba(0, 160, 119, .2);
}

.itemList li:last-child {
    border-bottom: none;
}